<template>
  <input
    :id="id"
    ref="input"
    :autocomplete="autocomplete"
    :autofocus="autofocus"
    :disabled="disabled"
    :min="min"
    :max="max"
    :minlength="minlength"
    :maxlength="maxlength"
    :pattern="pattern"
    :step="step"
    :value="modelValue"
    :type="type"
    :placeholder="placeholder"
    :required="required"
    class="input"
    :class="{'is-warning' : !invalid && !validity_valid, 'is-danger' : invalid && !validity_valid}"
    @input="updateValidity(); $emit('update:modelValue', $event.target.value); $emit('input');"
    @invalid="updateValidity(); $emit('invalid'); invalid = true"
  >
</template>

<script>
export default {
  name: "Input",
  props: [
    "autocomplete", 
    "autofocus", 
    "disabled",
    "id",
    "min", 
    "max", 
    "minlength", 
    "maxlength", 
    "pattern", 
    "placeholder", 
    "required", 
    "step", 
    "type", 
    "modelValue"
  ],
  emits: ["update:modelValue", "invalid", "input"],
  data() {
    return {
      invalid: false,
      validity_valid : true
    };
  },
  methods: {
    valid() {
      return !this.invalid || this.$refs['input'].validity.valid;
    },
    updateValidity() {
      this.validity_valid = this.$refs['input'].validity.valid;
    }
  }
};
</script>
