<template>
  <div class="container panel has-background-white">
    <h1 class="panel-heading is-4 has-text-centered">
      Gestão de formulários e modelos - {{ event.name }}
    </h1>
    <div class="section pb-0">
      <b>Formulário de Inscrição: </b><span>{{ signup_form?.title }}</span><br>
      <b>Formulário de Feedback: </b><span>{{ feedback_form?.title }}</span><br>
      &nbsp;<br>
      <b>Bilhete: </b><span>Criado</span><br>
      <b>Certificado: </b><span>Criado</span><br>
    </div>
    <hr>
    <div class="section pt-0 flex row sa">
      <div class="flex column">
        <div
          class="modal"
          :class="{'is-active' : selected_form !== undefined}"
        >
          <div
            class="modal-background"
            @click="close"
          />
          <div class="modal-card">
            <header class="modal-card-head">
              <p class="modal-card-title">
                {{ selected_form?.title }}
              </p>
              <button
                class="delete"
                aria-label="close"
                @click="close"
              />
            </header>
            <section
              :key="selected_form"
              class="modal-card-body"
            >
              <div
                v-if="!selected_form_questions"
                class="flex column ca vca"
              >
                <span class="subtitle">A carregar formulário</span>
                <span
                  class="loader mt-3"
                  style="width: 50px; height: 50px;"
                />
              </div>
              <div
                v-for="section in selected_form_questions.form.sections"
                v-else
                :key="section.id"
                class="flex column vca"
              >
                <span class="title">{{ section.section_name }}</span>
                <div
                  v-for="question in section.questions"
                  :key="question.id"
                  class="mb-4"
                  style="text-align: center; width: 100%;"
                >
                  <span
                    class="subtitle mb-1"
                    :class="{'required' : question.required}"
                  >{{ question.question }}</span> <br><span>({{ getFieldTypeTitle(question.properties.field_type) }})</span>
                  <div
                    v-if="question.properties.field_type === 'text_exposition'"
                    class="box content"
                    v-html="question.properties.content"
                  />
                  <div
                    v-else-if="question.properties.field_type === 'selectboxes'"
                    class="box"
                    style="text-align: left;"
                  >
                    <ul
                      class="ml-4 mr-2"
                      style="list-style-type: square;"
                    >
                      <li
                        v-for="option in question.properties.options"
                        :key="option.id"
                      >
                        {{ option.question }}
                      </li>
                    </ul>
                    <div
                      v-if="question.properties.min_selected || question.properties.max_selected"
                      style="text-align: center;"
                    >
                      <hr>
                      <span class="is-size-4">Restrições</span><br>
                      <span v-if="question.properties.min_selected && question.properties.min_selected === question.properties.options.length">
                        Deve selecionar todas as opções<br>
                      </span>
                      <span v-else-if="question.properties.min_selected && question.properties.min_selected === question.properties.max_selected">
                        Deve selecionar {{ question.properties.max_selected }} {{ question.properties.max_selected == 1 ? 'opção' : 'opções' }}
                      </span>
                      <span v-else>
                        <span v-if="question.properties.min_selected">
                          Deve selecionar pelo menos {{ question.properties.max_selected }} {{ question.properties.min_selected == 1 ? 'opção' : 'opções' }}<br>
                        </span>
                        <span v-if="question.properties.max_selected">
                          Deve selecionar até {{ question.properties.max_selected }} {{ question.properties.max_selected == 1 ? 'opção' : 'opções' }}<br>
                        </span>
                      </span>
                    </div>
                  </div>
                  <div 
                    v-else-if="question.properties.field_type === 'multiplechoice'" 
                    class="box" 
                    style="text-align: left;"
                  >
                    <ul
                      class="ml-4 mr-2"
                      style="list-style-type: square;"
                    >
                      <li
                        v-for="option in question.properties.options"
                        :key="option.id"
                      >
                        {{ option.question }}
                      </li>
                    </ul>
                  </div>
                </div>
                <hr style="width: 100%;">
              </div>
            </section>
            <footer class="modal-card-foot flex row ca">
              <template v-if="!selected_active">
                <button
                  class="button"
                  @click="close"
                >
                  Cancelar
                </button>
                <button
                  class="button is-success"
                  :class="{'is-loading' : loading}"
                  @click="patchForm(event.id, selected_form.id, selected_form_type); loading = true"
                >
                  Usar este formulário
                </button>
              </template>
              <template v-else>
                Formulário ativo
              </template>
            </footer>
          </div>
        </div>
        <span class="title flex row ca mb-4">Formulários</span>
        <div class="flex row ca is-flex-grow-1">
          <Dropdown
            text="Formulário de Inscrição"
            :alignment="43"
            class="mr-2"
          >
            <div
              class="dropdown-item p-0 pl-2 pr-2"
            >
              <div class="control has-icons-left">
                <span class="icon"><i class="fas fa-search" /></span>
                <Input
                  v-model="search.signup"
                  placeholder="Pesquisar"
                  class="input is-expanded"
                  type="text"
                />
              </div>
            </div>
            <div
              v-if="signup_forms_filtered?.length == 0"
              style="display: contents;"
            >
              <hr class="dropdown-divider">
              <span class="dropdown-item prevent-overflow">
                Sem resultados...
              </span>
            </div>
            <div 
              v-for="form in signup_forms_filtered" 
              :key="form.id"
              style="display: contents"
            >
              <hr class="dropdown-divider">
              <a
                class="dropdown-item prevent-overflow"
                :class="{'is-active' : event.formsignupid == form.id}"
                style="white-space: break-spaces"
                @click="selected_form = form; selected_form_type='signup'; update_selected_form();"
              >
                {{ form.title }}
              </a>
            </div>
          </Dropdown>
          <Dropdown
            text="Formulário de Feedback"
            :alignment="43"
            class="mr-2"
          >
            <div
              class="dropdown-item p-0 pl-2 pr-2"
            >
              <div class="control has-icons-left">
                <span class="icon"><i class="fas fa-search" /></span>
                <Input
                  v-model="search.feedback"
                  placeholder="Pesquisar"
                  class="input is-expanded"
                  type="text"
                />
              </div>
            </div>
            <div
              v-if="feedback_forms_filtered?.length == 0"
              style="display: contents;"
            >
              <hr class="dropdown-divider">
              <span class="dropdown-item prevent-overflow">
                Sem resultados...
              </span>
            </div>
            <div 
              v-for="form in feedback_forms_filtered" 
              :key="form.id"
              style="display: contents"
            >
              <hr class="dropdown-divider">
              <a
                class="dropdown-item prevent-overflow"
                :class="{'is-active' : event.formfeedbackid == form.id}"
                style="white-space: break-spaces"
                @click="selected_form = form; selected_form_type='feedback'; update_selected_form();"
              >
                {{ form.title }}
              </a>
            </div>
          </Dropdown>
        </div>
      </div>
      <div class="flex column">
        <span class="title flex row ca mb-4">Modelos</span>
        <div class="flex row ca">
          <button
            type="button"
            class="button is-primary mr-2"
          >
            Gerir bilhetes
          </button>
          <button
            type="button"
            class="button is-primary ml-2"
          >
            Gerir certificados
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { API } from "@/http-common";
import Dropdown from "@/components/Dropdown"
import Input from "@/components/Input"
import {stringIn} from "@/common"
import {getFieldTypeTitle, getCookie} from "@/common.js"
export default {
  name: "ChooseForm",
  components: {Dropdown, Input},
  data() {
    return {
      event : this.$route.params.id,
      signup_forms : undefined,
      feedback_forms : undefined,
      search : {
        signup : "",
        feedback : ""
      },
      selected_form : undefined,
      selected_form_type : undefined,
      selected_form_questions : undefined,
      loading : undefined
    }
  },
  created() {
    API.get("event/".concat(this.$route.params.id))
      .then((response) => 
        this.event = response.data
      );
    API.get("form/?type=1")
      .then((response) => 
        this.signup_forms = response.data
      )
    API.get("form/?type=3")
      .then((response) => 
        this.feedback_forms = response.data
      )
  },
  computed : {
    signup_form() {
      return this?.signup_forms?.filter((x) => x.id === this.event.formsignupid)[0];
    },
    feedback_form() {
      return this?.feedback_forms?.filter((x) => x.id === this.event.formfeedbackid)[0];
    },
    signup_forms_filtered() {
      return this?.signup_forms?.filter((x) => stringIn(x.title, this.search.signup));
    },
    feedback_forms_filtered() {
      return this?.feedback_forms?.filter((x) => stringIn(x.title, this.search.feedback));
    },
    selected_active() {
      if (!this.selected_form || !this.event)
        return undefined;
      if (this.selected_form_type == "feedback")
        return this.event.formfeedbackid == this.selected_form.id;
      else
        return this.event.formsignupid == this.selected_form.id;
    }
  },
  methods : {
    async update_selected_form() {
      if (this.selected_form) {
        await API.get("form/".concat(this.selected_form.id))
        .then((response) => 
          this.selected_form_questions = response.data
        )
      }
      return undefined;
    },
    close() {
      this.selected_form = undefined; this.selected_form_type = undefined; this.selected_form_questions = undefined;
    },
    getFieldTypeTitle,
    patchForm(event, form, formType) {
      var data;
      if (formType == 'signup') {
        data = {"formsignupid" : form};
      }
      else if (formType == 'feedback') {
        data = {"formfeedbackid" : form}
      }
      else
        console.log("ERROR")
      const options = {
        method: "patch",
        url: "/event/" + event + "/",
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data,
        transformResponse: [
          (data) => {
            return data;
          },
        ],
      };
      API(options)
        .then(() => {
          this.loading = undefined;
          if (formType == 'feedback')
            this.event.formfeedbackid = form;
          else
            this.event.formsignupid = form;})
        .catch((error) => {
          console.log(error); 
          this.loading = 'error';
        });
    },
  } 
};
</script>

<style scoped lang="scss">
  .noafter:after {
    content: '' !important; 
  }

  th, td, a {
    vertical-align: middle !important;
  }
</style>
